import React, { PropTypes, Component } from "react";
import ReactDOM  from "react-dom";

import d3 from "d3";
import ReStock from "react-stockcharts";
import Cookies from "js-cookie";

import PureComponent from "utils/PureComponent";
import * as ChartTypes from "constants/ChartTypes";

import BackgroundText from "./BackgroundText";

var { ChartCanvas, Chart, DataSeries, EventCapture } = ReStock;

var { CandlestickSeries, HistogramSeries, LineSeries, AreaSeries } = ReStock.series;
var { KagiSeries, RenkoSeries, PointAndFigureSeries } = ReStock.series;
var { BollingerSeries, MACDSeries, RSISeries, StochasticSeries } = ReStock.series;

var { MouseCoordinates } = ReStock.coordinates;

var { TooltipContainer, OHLCTooltip, MovingAverageTooltip, BollingerBandTooltip, MACDTooltip, RSITooltip, StochasticTooltip } = ReStock.tooltip;
var { StockscaleTransformer, HeikinAshiTransformer, KagiTransformer, PointAndFigureTransformer, RenkoTransformer } = ReStock.transforms;

var { XAxis, YAxis } = ReStock.axes;

var { MACD, EMA, SMA, BollingerBand, RSI, FullStochasticOscillator } = ReStock.indicator;

var margin = {left: 70, right: 70, top:10, bottom: 30};
var dateFormat = d3.time.format("%Y-%m-%d");

function getXAxis(show, verticalGrid) {
	return show
		? <XAxis axisAt="bottom" orient="bottom" {...verticalGrid} />
		: <XAxis axisAt="bottom" orient="bottom" showTicks={false} outerTickSize={0} />;
}

function getMainChartType(type) {
	switch (type.value) {
		case ChartTypes.AREA.value: {
			return AreaSeries;
		}
		case ChartTypes.LINE.value: {
			return LineSeries;
		}
		case ChartTypes.CANDLESTICK.value: {
			return CandlestickSeries;
		}
		case ChartTypes.HEIKIN_ASHI.value: {
			return CandlestickSeries;
		}
		case ChartTypes.RENKO.value: {
			return RenkoSeries;
		}
		case ChartTypes.KAGI.value: {
			return KagiSeries;
		}
		case ChartTypes.POINT_FIGURE.value: {
			return PointAndFigureSeries;
		}
	}
}

function getConfig(each, dy, movingAverageTooltipExists, chartHeight, verticalGrid, logger) {
	var chart, dataSeries, toolTip, toolTipDY, indicator;
	var { chartId, dataSeriesId, options } = each;
	// console.log(options);
	switch (each.type) {
		case "BollingerBand": {
			dataSeries = <DataSeries key={dataSeriesId} id={dataSeriesId} indicator={BollingerBand} options={options}>
				<BollingerSeries />
			</DataSeries>;
			var start = dy + 20;
			toolTipDY = start + 5;
			toolTip = <BollingerBandTooltip forChart={chartId} forDataSeries={dataSeriesId} key={`${chartId}-${dataSeriesId}`}
				onClick={logger.bind(null, { chartId, dataSeriesId, indicatorType: each.type })} origin={[-48, start]} />;
			break;
		}
		case "SMA": {
			indicator = SMA;
		}
		case "EMA": {
			if (!indicator) indicator = EMA;
			dataSeries = <DataSeries key={dataSeriesId} id={dataSeriesId} indicator={indicator} options={options} >
				<LineSeries/>
			</DataSeries>;
			if (!movingAverageTooltipExists) {
				var start = dy + 5;
				toolTipDY = start + 25;
				toolTip = <MovingAverageTooltip forChart={chartId} key={`${chartId}-${dataSeriesId}`}
					onClick={logger.bind(null, { chartId, dataSeriesId, indicatorType: each.type })} origin={[-45, start]} />;
			}
			break;
		}
		case "MACD": {
			chart = (showXAxis, dataSeries, noOfChartsAfter = 0) => <Chart id={chartId} key={chartId} yMousePointerDisplayLocation="right" yMousePointerDisplayFormat={(y) => y.toFixed(2)}
						height={chartHeight} origin={(w, h) => [0, h - chartHeight - chartHeight * noOfChartsAfter]} padding={{ top: 5, right: 0, bottom: 5, left: 0 }} >
				{ getXAxis(showXAxis, verticalGrid) }
				<YAxis axisAt="right" orient="right" ticks={2}/>
				<DataSeries id={dataSeriesId} indicator={MACD} options={options} >
					<MACDSeries />
				</DataSeries>
			</Chart>;
			toolTip = <MACDTooltip ref="MACDTooltip" forChart={chartId} forDataSeries={dataSeriesId} key={`${chartId}-${dataSeriesId}`}
				onClick={logger.bind(null, { chartId, dataSeriesId, indicatorType: each.type })} origin={[-48, 15]}/>;
			toolTipDY = dy;
			break;
		}
		case "RSI": {
			chart = (showXAxis, dataSeries, noOfChartsAfter = 0) => <Chart id={chartId} key={chartId} yMousePointerDisplayLocation="right" yMousePointerDisplayFormat={(y) => y.toFixed(2)}
						height={chartHeight} origin={(w, h) => [0, h - chartHeight - chartHeight * noOfChartsAfter]} padding={{ top: 0, right: 0, bottom: 0, left: 0 }} >
				{ getXAxis(showXAxis, verticalGrid) }
				<YAxis axisAt="right" orient="right" ticks={2}/>
				<DataSeries id={dataSeriesId} indicator={RSI} options={options} >
					<RSISeries />
				</DataSeries>
			</Chart>;
			toolTip = <RSITooltip forChart={chartId} forDataSeries={dataSeriesId} key={`${chartId}-${dataSeriesId}`}
				onClick={logger.bind(null, { chartId, dataSeriesId, indicatorType: each.type })} origin={[-48, 15]}/>;
			toolTipDY = dy;
			break;
		}
		case "FullStochasticOscillator": {
			chart = (showXAxis, dataSeries, noOfChartsAfter = 0) => <Chart id={chartId} key={chartId} yMousePointerDisplayLocation="right" yMousePointerDisplayFormat={(y) => y.toFixed(2)}
						height={chartHeight} origin={(w, h) => [0, h - chartHeight - chartHeight * noOfChartsAfter]} padding={{ top: 5, right: 0, bottom: 5, left: 0 }} >
				{ getXAxis(showXAxis, verticalGrid) }
				<YAxis axisAt="right" orient="right" ticks={2}/>
				<DataSeries id={dataSeriesId} indicator={FullStochasticOscillator} options={options} >
					<StochasticSeries />
				</DataSeries>
			</Chart>;
			toolTip = <StochasticTooltip forChart={chartId} forDataSeries={dataSeriesId} key={`${chartId}-${dataSeriesId}`}
				onClick={logger.bind(null, { chartId, dataSeriesId, indicatorType: each.type })} origin={[-48, 15]}>Full STO</StochasticTooltip>;
			toolTipDY = dy;
			break;
		}
	}
	return { chart, dataSeries, toolTip, toolTipDY };
}

class ChartWidget extends PureComponent {

	componentWillUnmount() {
		// console.log("componentWillUnmount");
	}
	componentWillMount() {
		// console.log("componentWillMount");
	}
	componentDidMount() {
		var cookie = Cookies.get("demoComplete");

		if (cookie !== undefined ) {
			var { width, height } = this.props;
			var gridHeight = height - margin.bottom;

			var el = ReactDOM.findDOMNode(this.refs.canvas).parentNode;

			var tooltip = ReactDOM.findDOMNode(this.refs.MACDTooltip).innerHTML.replace(/data-reactid="[^"]*"/g, "");
			// d3.select(el).append("svg").attr("width", 400).attr("height", 20).append(svgTooltip);
			// tooltip.createElement
			d3.select(el)
				.append("div")
					.attr("style", `position: absolute; top: ${gridHeight - 125}px; left: 15px; height: 20px; width: 400px`)
					.attr("data-intro", "Click on tooltip of any indicator to change its settings")
					.attr("data-step", "4")
					.attr("class", "removemeafterload")
				.append("svg")
					.attr("width", 400).attr("height", 20)
				.append("g")
					.attr("transform", `translate(${ 7.5 }, ${ 15.5 })`)
				.html(tooltip);
		}
	}
	render() {
		var { width, height, resized, data, indicators, type, transforms, exchange, security, grid } = this.props;
		var { onIndicatorSettingsChange, onChartSettingsChange } = this.props;

		var chartSettings = transforms[transforms.length - 1].options;
		// console.log(type, exchange, security);
		var MainChartSeries = getMainChartType(type);

		var gridHeight = height - margin.top - margin.bottom;
		var gridWidth = width - margin.left - margin.right;

		var chartHeight = Math.min(gridHeight / 5, 125);

		var horizontalGrid = grid
			? { innerTickSize: -1 * gridWidth, tickStrokeOpacity: 0.2 }
			: { };

		var verticalGrid = grid
			? { innerTickSize: -1 * gridHeight, tickStrokeOpacity: 0.2 }
			: { };

		var chartId = 3, dataSeriesId = 1;
		var charts = [
			(showXAxis, dataSeries, noOfChartsAfter = 0) => (<Chart id={0} key={0} yMousePointerDisplayLocation="right"
					height={gridHeight - chartHeight * (noOfChartsAfter - 1)} yMousePointerDisplayFormat={(y) => y.toFixed(2)}>
				<YAxis axisAt="right" orient="right" ticks={5} {...horizontalGrid} />
				<DataSeries id={0} yAccessor={MainChartSeries.yAccessor} >
					<MainChartSeries />
				</DataSeries>
				{dataSeries}
			</Chart>),
			(showXAxis, dataSeries, noOfChartsAfter = 0) => (<Chart id={1} key={1} yMousePointerDisplayLocation="left" yMousePointerDisplayFormat={d3.format(".4s")}
					height={chartHeight} origin={(w, h) => [0, h - chartHeight - chartHeight * noOfChartsAfter]} >
				<YAxis axisAt="left" orient="left" ticks={5} tickFormat={d3.format("s")}/>
				{ getXAxis(showXAxis, verticalGrid) }
				<DataSeries id={0} yAccessor={(d) => d.volume} >
					<HistogramSeries fill={(d) => d.close > d.open ? "#6BA583" : "red"} />
				</DataSeries>
			</Chart>),
		];
		var dataSeries = [];
		var toolTips = [];
		var toolTipDY = 5;

		var movingAverageTooltipExists = false;
		for (var i = 0; i < indicators.length; i++) {
			var each = indicators[i];
			var config = getConfig(each, toolTipDY, movingAverageTooltipExists, chartHeight, verticalGrid, onIndicatorSettingsChange);
			if (config.chart) {
				charts.push(config.chart);
			}
			if (config.dataSeries) {
				dataSeries.push(config.dataSeries);
			}
			if (config.toolTip) {
				var ToolTip = config.toolTip;
				var forChart = (config.chart) ? chartId - 1 : 1;
				toolTips.push(config.toolTip); // <ToolTip key={`${ chartId }-${ dataSeriesId }`} forChart={forChart} origin={[-48, toolTipDY]} />);
				toolTipDY = config.toolTipDY;
			}
			if (each.type === "EMA" || each.type === "SMA") movingAverageTooltipExists = true;
		}
		var chartsJSX = [];
		for (var i = 0; i < charts.length; i++) {
			var each;

			if (i === charts.length - 1) {
				// last
				each = charts[i](true, dataSeries, charts.length - i - 1);
			} else {
				each = charts[i](false, dataSeries, charts.length - i - 1);
			}
			chartsJSX.push(each);
		};
		// console.log(chartsJSX[2]);
		return (
			<ChartCanvas ref="canvas" width={width} height={height}
				margin={margin} initialDisplay={200}
				dataTransform={transforms}
				data={data} type="hybrid">
				{chartsJSX}
				<BackgroundText x={width / 2 - margin.left} y={height / 2} height={height} security={security} />
				<MouseCoordinates xDisplayFormat={dateFormat} type="crosshair" />
				<EventCapture mouseMove={true} zoom={true} pan={true} mainChart={1} defaultFocus={false} />
				<TooltipContainer>
					<OHLCTooltip forChart={0} origin={[-48, 0]} onClick={onChartSettingsChange.bind(null, type, chartSettings)} />
					{toolTips}
				</TooltipContainer>
			</ChartCanvas>
		);
	}
}

/* ChartWidget.propTypes = {
};
*/
export default ChartWidget;


