import React from "react";

class BackgroundText extends React.Component {
	render() {
		var { x, y, height, security } = this.props;
		var { interval } = this.context;
		return (
			<text x={x} y={y}
				opacity={0.4}
				fontSize={0.08 * height}
				fill="steelblue"
				textAnchor="middle">{`${ security }(NASDAQ), ${ interval }`}
			</text>
		);
	}
}

BackgroundText.contextTypes = {
	interval: React.PropTypes.string.isRequired,
};

export default BackgroundText;


