import { TOGGLE_HEADER, TOGGLE_RIGHT_PANE, TOGGLE_LEFT_NAV, ESCAPE } from "../constants/ActionTypes";
import { WINDOW_RESIZE } from "../constants/ActionTypes";


export function toggleHeader() {
	return {
		type: TOGGLE_HEADER
	};
}

export function toggleRightPane() {
	return (dispatch, getState) => {
		dispatch(toggleRightPaneSync());
		setTimeout(() => {
			dispatch(windowResize());
		}, 20);
	};
}

export function toggleRightPaneSync() {
	return {
		type: TOGGLE_RIGHT_PANE
	};
}

export function toggleLeftNav() {
	return {
		type: TOGGLE_LEFT_NAV
	};
}

export function escape() {
	return {
		type: ESCAPE
	};
}

export function windowResize() {
	return {
		type: WINDOW_RESIZE,
	};
}

